import { Component, Inject, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { MatColors } from '@fuse/mat-colors';
import { CalendarEvent } from 'angular-calendar';

@Component({
  selector: 'app-align-dash-cal-dialog',
  templateUrl: './align-dash-cal-dialog.component.html',
  styleUrls: ['./align-dash-cal-dialog.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class AlignDashCalDialogComponent {
  event: CalendarEvent;
  dialogTitle: string;
  eventForm: FormGroup;
  action: string;
  presetColors = MatColors.presets;

  constructor(
    public dialogRef: MatDialogRef<AlignDashCalDialogComponent>,
    @Inject(MAT_DIALOG_DATA) private data: any,
    private formBuilder: FormBuilder) {

      this.event = data.event;
        this.action = data.action;

        if ( this.action === 'edit' )
        {
            this.dialogTitle = this.event.title;
        }
        else
        {
            this.dialogTitle = 'New Event';
            this.event = {
                title: data.title,
                start  : data.date
            };
        }

        this.eventForm = this.createEventForm();

  }

  createEventForm()
    {
        return new FormGroup({
            title : new FormControl(this.event.title),
            holiday_date : new FormControl(this.event.start)
        });
    }

}
