import { AlignDashService } from './align-dash.service';
import { ApiLoaderService } from '@fuse/services/api-loader.service';
import { AlignService } from './align.service';
import { Component, OnInit, ViewEncapsulation, ViewChild, ViewChildren } from '@angular/core';
import * as shape from 'd3-shape';
import { fuseAnimations } from '@fuse/animations';
import { MatDialog, MatDialogRef } from '@angular/material';
import { Subject } from 'rxjs';
import { startOfDay, isSameDay, isSameMonth } from 'date-fns';
import { CalendarEvent, CalendarEventAction, CalendarEventTimesChangedEvent, CalendarMonthViewDay } from 'angular-calendar';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';
import { DATA } from './dashboard-calendar'
import { AlignDashCalDialogComponent } from './align-dash-cal-dialog/align-dash-cal-dialog.component';
import { FormGroup } from '@angular/forms';
import { NgForm } from '@angular/forms';
import { FusePerfectScrollbarDirective } from '@fuse/directives/fuse-perfect-scrollbar/fuse-perfect-scrollbar.directive';
import { ChatService } from './chat.service';
import { ToasterService } from '@fuse/services/toaster-service.service';

@Component({
  selector: 'app-align',
  templateUrl: './align.component.html',
  styleUrls: ['./align.component.scss'],
  animations   : fuseAnimations,
  providers: [ AlignDashService ]
})
export class AlignComponent implements OnInit {
  projects: any[];
  selectedProject: any;
  widgets: any;
  widget5: any = {};
  dateNow = Date.now();

  // calendar
  view: string;
  viewDate: Date;
  events: CalendarEvent[];
  public actions: CalendarEventAction[];
  activeDayIsOpen: boolean;
  refresh: Subject<any> = new Subject();
  dialogRef: any;
  confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;
  selectedDay: any;

  // Chat
    user: any;
    chat: any;
    chatdialog: any;
    contact: any;
    replyInput: any;
    selectedChat: any;
    @ViewChild(FusePerfectScrollbarDirective) directiveScroll: FusePerfectScrollbarDirective;
    @ViewChildren('replyInput') replyInputField;
    @ViewChild('replyForm') replyForm: NgForm;

  constructor(private alignservice: AlignService, 
                public dialog: MatDialog,
                private chatService: ChatService,
                private apiloader: ApiLoaderService,
                private toast: ToasterService,
                private aligndashService: AlignDashService) { 
        
        this.projects = this.alignservice.projects;
        this.widgets = this.alignservice.widgets;

        this.widget5 = {
          currentRange  : 'TW',
          xAxis         : true,
          yAxis         : true,
          gradient      : false,
          legend        : false,
          showXAxisLabel: false,
          xAxisLabel    : 'Days',
          showYAxisLabel: false,
          yAxisLabel    : 'Isues',
          scheme        : {
              domain: ['#42BFF7', '#C6ECFD', '#C7B42C', '#AAAAAA']
          },
          onSelect      : (ev) => {
              console.log(ev);
          },
          supporting    : {
              currentRange  : '',
              xAxis         : false,
              yAxis         : false,
              gradient      : false,
              legend        : false,
              showXAxisLabel: false,
              xAxisLabel    : 'Days',
              showYAxisLabel: false,
              yAxisLabel    : 'Isues',
              scheme        : {
                  domain: ['#42BFF7', '#C6ECFD', '#C7B42C', '#AAAAAA']
              },
              curve         : shape.curveBasis
          }
      };

      setInterval(() => {
        this.dateNow = Date.now();
    }, 1000);

    this.view = 'month';
        this.viewDate = new Date();
        this.activeDayIsOpen = true;
        this.selectedDay = {date: startOfDay(new Date())};

        this.actions = [ ];
  }

  ngOnInit() {
      this.getHolidays();
    this.user = this.chatService.user;
    this.chatService.onChatSelected
        .subscribe(chatData => {
            if ( chatData )
            {
                this.selectedChat = chatData;
                this.contact = chatData.contacts;
                this.chatdialog = chatData.dialog;
                this.readyToReply();
            }
        });
  }

    setEvents()
    {
        this.events = this.events.map(item => {
            item.actions = this.actions;
            return item;
        });
    }

    getHolidays(){
        this.apiloader.setLoader(true);
        this.aligndashService.getData('holidays').subscribe(response => {
            if (response.data){
                this.apiloader.setLoader(false);
                 const events = response.data;
                 for (let i = 0; i < events.length; i++){
                    events[i].title = events[i].details;
                    events[i].start = new Date(events[i].date);
                    events[i].color = {
                        primary  : '#ad2121',
                        secondary: '#FAE3E3'
                    };
                    events[i].actions = this.actions;
                 }
                 this.events = events;
                 this.setEvents();
            }
        }, error => {
            this.apiloader.setLoader(false);
            this.toast.showError(error);
        });
    }

    beforeMonthViewRender({header, body})
    {
        body.forEach((day) => {
            const event = day.events;
            if (event.length){
                event.forEach(item => {
                    day.cssClass = 'holiday';
                });
            }
        });
    }

    dayClicked(day: CalendarMonthViewDay): void
    {
        const date: Date = day.date;
        const events: CalendarEvent[] = day.events;

        if ( isSameMonth(date, this.viewDate) )
        {
            if ( (isSameDay(this.viewDate, date) && this.activeDayIsOpen === true) || events.length === 0 )
            {
                this.activeDayIsOpen = false;
            }
            else
            {
                this.activeDayIsOpen = true;
                this.viewDate = date;
            }
        }
        this.selectedDay = day;
        this.refresh.next();
    }

    editEvent(action: string, event: CalendarEvent)
    {
        const eventIndex = this.events.indexOf(event);

        this.dialogRef = this.dialog.open(AlignDashCalDialogComponent, {
            panelClass: 'event-form-dialog',
            data      : {
                event : event,
                action: action
            }
        });

        this.dialogRef.afterClosed()
            .subscribe(response => {
                if ( !response )
                {
                    return;
                }
                const actionType: string = response[0];
                const formData: FormGroup = response[1];
                switch ( actionType )
                {
                    /**
                     * Save
                     */
                    case 'save':

                        this.events[eventIndex] = Object.assign(this.events[eventIndex], formData.getRawValue());
                        this.refresh.next(true);

                        break;
                    /**
                     * Delete
                     */
                    case 'delete':

                        this.deleteEvent(event);

                        break;
                }
            });
    }

    addEvent(): void
    {
        this.dialogRef = this.dialog.open(AlignDashCalDialogComponent, {
            panelClass: 'event-form-dialog',
            data      : {
                action: 'new',
                date  : this.selectedDay.date
            }
        });
        this.dialogRef.afterClosed()
            .subscribe((response: FormGroup) => {
                if ( !response )
                {
                    return;
                }
                const newEvent = response.getRawValue();
                newEvent.actions = this.actions;
                this.events.push(newEvent);
                this.refresh.next(true);
            });
    }

    deleteEvent(event)
    {
        this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
            disableClose: false
        });

        this.confirmDialogRef.componentInstance.confirmMessage = 'Are you sure you want to delete?';

        this.confirmDialogRef.afterClosed().subscribe(result => {
            if ( result )
            {
                const eventIndex = this.events.indexOf(event);
                this.events.splice(eventIndex, 1);
                this.refresh.next(true);
            }
            this.confirmDialogRef = null;
        });

    }

    //Chat

    selectContact()
    {
        /* this.chatService.selectContact(this.contact); */
    }

    readyToReply()
    {
        setTimeout(() => {
            this.replyForm.reset();
            this.focusReplyInput();
            this.scrollToBottom();
        });

    }

    focusReplyInput()
    {
        setTimeout(() => {
            this.replyInput.focus();
        });
    }

    scrollToBottom(speed?: number)
    {
        speed = speed || 400;
        if ( this.directiveScroll )
        {
            this.directiveScroll.update();

            setTimeout(() => {
                this.directiveScroll.scrollToBottom(0, speed);
            });
        }
    }

    reply(event)
    {
        /* // Message
        const message = {
            who    : this.user.id,
            message: this.replyForm.form.value.message,
            time   : new Date().toISOString()
        };

        // Add the message to the chat
        this.chatdialog.push(message);

        // Update the server
        this.chatService.updateDialog(this.selectedChat.chatId, this.chatdialog).then(response => {
            this.readyToReply();
        }); */

    }


}
