import { AlignDashService } from './align-dash.service';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { AlignComponent } from './align.component';
import { AlignService } from './align.service';
import { NgxChartsModule } from '@swimlane/ngx-charts';
import { FuseSharedModule } from '@fuse/shared.module';
import { FuseWidgetModule } from '@fuse/components/widget/widget.module';
import { MatButtonModule, MatSidenavModule, MatDatepickerModule,
         MatMenuModule, MatFormFieldModule, MatIconModule, MatInputModule, MatSlideToggleModule, MatToolbarModule } from '@angular/material';
import { CalendarModule } from 'angular-calendar';
import { ColorPickerModule } from 'ngx-color-picker';
import { FuseConfirmDialogModule } from '@fuse/components';
import { AlignDashCalDialogComponent } from './align-dash-cal-dialog/align-dash-cal-dialog.component';
import { ChatService } from './chat.service';

const routes: Routes = [
  {
      path     : '**',
      component: AlignComponent,
      resolve  : {
          data: AlignService,
          chat: ChatService
      }
  }
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgxChartsModule,
    FuseSharedModule,
    FuseWidgetModule,
    MatSidenavModule,
    MatDatepickerModule,
    MatFormFieldModule,
    MatIconModule,
    MatInputModule,
    MatSlideToggleModule,
    MatToolbarModule,
    MatButtonModule,
    MatMenuModule,
    CalendarModule.forRoot(),
    ColorPickerModule,
    FuseConfirmDialogModule
  ],
  declarations: [AlignComponent, AlignDashCalDialogComponent],
  providers: [AlignService, ChatService, AlignDashService],
  entryComponents: [ AlignDashCalDialogComponent ]
})
export class AlignModule { }
